.main-header {
  background: linear-gradient(90deg, #1c3477 0%, #1c3477 0.01%, #1a3578 39.11%, #73184b 75.5%, #b4022a 100%);
}

.main-header__container {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 0 103px;

  @include vp-1365 {
    padding: 0 45px;
  }
}

.main-header__link {
  padding: 30px 0;
  color: #ffffff;
  text-align: right;
  font-size: 26px;
  font-weight: 700;
  line-height: 26px;

  @include vp-1365 {
    font-size: 24px;
    line-height: 24px;
  }
}

.main-header__logo-link {
  padding: 22px;
  display: block;
}

.main-header__wrapper-logo {
  background-color: #102654;
  display: inline-block;
  margin-top: 40px;
}
